body {
    margin: 0;
    padding: 0;
    font-family: "Quattrocento", serif;
    font-weight: 400; /* Regular weight */
    line-height: 1.15;
    font-size: 120%;
}

h1 {
    font-family: "Roboto", Arial, Helvetica, sans-serif;
    font-optical-sizing: auto;
    font-weight: 700; /* Font weight set to 700 (bold) */
    font-style: normal;
}

h3 {
    font-family: "Quattrocento Sans", Arial, Helvetica, sans-serif;
    font-optical-sizing: auto;
    font-weight: 700; /* Font weight set to 700 (bold) */
    font-style: normal;
}

.container {
    max-width: 60%; /* Adjust the maximum width as needed */
    margin: 0 auto; /* This centers the container horizontally */
    padding: 0 20px; /* Add some padding to the sides */
}

.playfair-display-main-title {
  font-family: "Playfair Display", serif;
  font-optical-sizing: auto;
  font-weight: 700; /* Font weight set to 700 (bold) */
  font-style: normal;
}

.quattrocento-sans-navbar a {
    font-family: "Quattrocento Sans", Arial, Helvetica, sans-serif;
    font-optical-sizing: auto;
    font-weight: 700; /* Font weight set to 700 (bold) */
    font-style: normal;
}

.quattrocento-sans-paper-titles {
    font-family: "Quattrocento Sans", Arial, Helvetica, sans-serif;
    font-optical-sizing: auto;
    font-weight: 700; /* Font weight set to 700 (bold) */
    font-style: normal;
    margin-bottom: -1em; /* Add this line to remove space below the title */
}

/* Style for the nav bar background */
.nav-background-bar {
  background-color: #2b0b43; /*#601a94; */
  height: 45px; /* Adjust height as needed */
  width: 100%;
  position: fixed; /* Position the black bar absolutely absolute */
  top: 0; /* Align the black bar to the top of the navbar */
  z-index: -1; /* Set z-index to make sure it's behind the navbar content */
}

/* Style for the navbar */
.navbar {
  position: fixed; /*relative;*/
}

/* Style for the navigation links */
.navbar nav a {
  display: inline-block;
  padding: 10px 20px;
  margin: 0 5px;
  color: white;
  text-decoration: none;
  border-radius: 5px;
  /* transition: background-color 0.3s; */
  position: relative; /* Needed for pseudo-element positioning */
}

.navbar nav a:hover:after {
  content: ""; /* Empty content for the pseudo-element */
  position: absolute; /* Position the bar under the link */
  bottom: 5px; /* Align bottom of the bar with the link */
  left: 50%; /* Center the bar horizontally under the text */
  transform: translateX(-50%); /* Center the bar using transform */
  width: 60%; /* Make the bar as wide as the link text */
  height: 3px; /* Adjust height as desired for the bar thickness */
  background-color: white; /* Set the bar color to white */
  opacity: 0.8; /* Make the bar fully visible on hover */
  transition: opacity 0.2s ease-in-out; /* Add smooth transition effect */
}

/* Banner */
.banner {
    background-image: url("/assets/images/remix.jpg"); 
    background-size: cover; /* Adjusts the background image size to cover the entire banner */
    background-position: center; /* Centers the background image within the banner */
    height: 250px; /* Adjust the height of your banner */
    color: white; /* Sets the text color to white for better contrast */
    padding-top: 45px; /* Adjusts the top padding to vertically center the text */
    display: flex; /* Use flexbox to center text */
    justify-content: center; /* Center content horizontally */
    align-items: center; /* Center content vertically */
}

.banner h1 {
    /*font-size: 60px; *//* Adjusts the font size of the title text */
    font-family: "Playfair Display", serif;
    font-size: calc(2vw + 2vh); /* Adjusts the font size of the title text responsively */
    max-width: 100%; /* Ensures the text does not exceed the width of the banner */
    font-optical-sizing: auto;
    font-weight: 700; /* Font weight set to 700 (bold) */
    font-style: normal;
    text-align: center; /* Center text within its container */
    margin: 0; /* Remove default margin */
}

/* Images */
.circular-image {
    border-radius: 50%; /* Creates a circular shape */
    width: 200px; /* Adjust the width as needed */
    height: 200px; /* Adjust the height to match the width */
    object-fit: cover; /* Ensures the image covers the entire box */
}

.profile-container {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

.profile {
    text-align: center;
    margin: 10px;
}

/* Links */
/* CSS for styling link text */
a {
    color: #2b0b43; /* Set the color of the link text */
    font-family: "Quattrocento Sans", Arial, Helvetica, sans-serif;
    font-optical-sizing: auto;
    font-weight: 700; /* Font weight set to 700 (bold) */
    font-style: normal;
}

/* CSS for styling link text on hover */
a:hover {
    color: red; /* Change the color of the link text on hover */
    text-decoration: underline; /* Add underline to links on hover */
}

